import React, { Component } from 'react';

import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import LinearProgress from '@material-ui/core/LinearProgress';
import Card from '@material-ui/core/Card';
import Snackbar from '@material-ui/core/Snackbar';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import AlarmIcon from '@material-ui/icons/Alarm';
import LoyaltyIcon from '@material-ui/icons/Loyalty';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';

import { QtumLightApi } from 'qtum-light-js';
import moment from 'moment';

import logo from './qtum-raffle.svg';
import './App.css';

const address = 'adcbf0ac6a351e5f0b86eec5a4df288e62abacc0';
const qtumLightApi = new QtumLightApi();

const { ADDRESS, ADDRESS_ARRAY } = qtumLightApi.DataTypes;

const ONE_QTUM = 100000000;

let refreshIntervalId;

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#343a40',
    },
    secondary: {
      main: '#e6ae49',
    }
  },
});

class App extends Component {
  state = {
    owner: '',
    players: [],
    balance: '',
    hasError: false,
    message: '',
    numberOfRaffle: '',
    contractLoaded: false,
    loading: false,
    selectedTab: 0,
    buyPricePerRaffle: 1,
    buyAmount: 1,
    buyPrice: 1,
    walletInitialized: false
  };

  componentDidMount() {
    this.loadWallet();
  }

  loadWallet = async () => {

    const isQtumLightInstalled = await qtumLightApi.isQtumLightInstalled();
    if (isQtumLightInstalled) {
      console.log('Qtum Light is installed!');
      const account = await qtumLightApi.getCurrentAddress();
      if (!account) {
        this.setState({ walletInitialized: true, snackbarOpen: true, snackbarMessage: 'Please sign in Qtum Light Wallet and refresh the page!'});
        return;
      }
      this.setState({account, walletInitialized: true});
      this.loadData();
      const endTime = new Date();
      endTime.setHours(23, 59, 59, 999);
      this.startCountdownTimer(endTime.getTime());
    } else {
      console.log('Qtum Light is not installed!');
      this.setState({ walletInitialized: true, snackbarOpen: true, snackbarMessage: 'Please install Qtum Light Wallet to use this app!' });
    }
  };

  loadData = async () => {

    this.setState({ loading: true, loadingMessage: 'Retrieving the raffle info from Qtum blockchain, please wait...' });
    try {
      const raffle = qtumLightApi.Contract(address);
      const network = 'TESTNET';
      const owner = await raffle.call('owner', null, null, [ADDRESS]);
      const playersArray = await raffle.call('getPlayers', null, null, [ADDRESS_ARRAY]);
      const players = playersArray[0];
      const contractAddress = address;
      const contractInfo = await qtumLightApi.queryContractInfo(address);
      const balance = contractInfo.balance / ONE_QTUM;
      this.setState({ loading: false, loadingMessage: '', raffle, owner, players, contractAddress, balance, network, contractLoaded: true });
    } catch(e) {
      console.error(e);
      this.setState({ loading: false, snackbarOpen: true, snackbarMessage: 'Something went wrong, please try again.' });
    }
  };

  handleTabChange = (event, value) => {
    this.setState({ selectedTab: value });
  };

  addRaffles = (number) => {
    let { buyAmount } = this.state;
    buyAmount = parseInt(buyAmount, 10) + number;
    const buyPrice = buyAmount * this.state.buyPricePerRaffle;
    this.setState({
      buyAmount,
      buyPrice
    });
  };

  renderBuy() {
    const { selectedTab, balance, owner } = this.state;
    return (
      <Card className="card big-card">
      <Tabs value={selectedTab} onChange={this.handleTabChange}>
        <Tab label="Buy" />
        <Tab label="Pick winner" />
      </Tabs>
        {selectedTab === 0 && <div>
          <p>* The price 1 QTUM per raffle.</p>
          <div className="buy-box">
            <div className="buy-icon">
              <LoyaltyIcon/>
            </div>
            <input
              required
              className="buy-amount"
              value={this.state.buyAmount}
              onChange={(ev) => this.handleBuyAmountChange(ev.target.value) }
              type="number"
              margin="normal"/>
              <div className="buy-price right"><div>@ {this.state.buyPrice} QTUM</div></div>
          </div>

          <div className="keys-container">
            <Button
              className="key-button"
              variant="outlined"
              color="secondary"
              onClick={() => this.addRaffles(1)}
              >
              +1 Raffle
            </Button>
            <Button
              className="key-button"
              variant="outlined"
              color="secondary"
              onClick={() => this.addRaffles(2)}
              >
              +2 Raffles
            </Button>
            <Button
              className="key-button"
              variant="outlined"
              color="secondary"
              onClick={() => this.addRaffles(5)}
              >
              +5
            </Button>
            <Button
              className="key-button"
              variant="outlined"
              color="secondary"
              onClick={() => this.addRaffles(10)}
              >
              +10
            </Button>
            <Button
              className="key-button"
              variant="outlined"
              color="secondary"
              onClick={() => this.addRaffles(100)}
              >
              +100
            </Button>
          </div>
            <Button
              id="buy-button"
              variant="raised"
              color="primary"
              onClick={this.onBuySubmit}
              >
              Buy
            </Button>
        </div>}
        {selectedTab === 1 && <div>
          <p>* Only the owner can pick a winner.</p>
          <div className="round-info-container">
            <div className="round-info"><span>Balance</span><span>{balance} QTUM</span></div>
            <div className="round-info"><span>Owner account</span><span>{owner}</span></div>
          </div>
          <Button
              id="claim-button"
              variant="raised"
              color="primary"
              onClick={this.onPickWinnerSubmit}
              >
              Pick Winner
          </Button>
        </div>}
      </Card>
    );
  }

  getAddressAbv = (address) => {
    if (address) {
      return address.substr(0, 5) + '...' + address.substr(address.length-4, 4);
    } else {
      return '';
    }
  };

  renderContractInfo() {
    const { contractAddress, balance } = this.state;
    const url = `//testnet.qtum.info/contract/${contractAddress}`;
    return (
      <Card className="card small-card">
        <div>
          <div className="card-title">Smart Contract</div>
          <hr className="card-title-divider"/>
          <div>Raffle will drain in:</div>
          <p className="round-countdown-timer">{this.state.countdownTimer} </p>
          <hr/>
          <div className="round-info"><span>Total QTUM</span><span>{balance} QTUM</span></div>
          <div className="round-info"><span>Contract Address</span><a href={url} target="_blank" className="contract-link">{this.getAddressAbv(contractAddress)}</a></div>
          <div className="round-info"><span>Source Code</span><a href="https://github.com/QtumLightProject/qtum-raffle" target="_blank" className="contract-link">GitHub | Qtum Raffle</a></div>
        </div>
      </Card>
    );
  }

  handleBuyAmountChange = (amount) => {
    const buyAmount = amount.replace(/^0+(?!\.|$)/, '').replace(/[^0-9 .]+/g,'').replace(/(\d)(?=(\d\d\d)+(?!\d))/g, "$1");
    const buyPrice = buyAmount * this.state.buyPricePerRaffle;
    this.setState({
      buyAmount,
      buyPrice
    });
  };

  onBuySubmit = async event => {
    event.preventDefault();
    const { buyPrice, raffle } = this.state;
    this.setState({snackbarOpen: true, snackbarMessage: 'Please confirm the transaction in the Qtum Light Wallet to complete the purchase.'});
    try {
      const qtum = buyPrice * ONE_QTUM;
      const tx = await raffle.send('buy', null, null, qtum, 0.0000004, 2500000);
      this.setState({snackbarOpen: false, loading: true, loadingMessage: 'Writing to the blockchain, please wait...'});
      await raffle.queryConfirmation(tx);
      this.setState({loading: false, snackbarOpen: true, snackbarMessage: 'Purchased successfully, good luck!'});
      this.loadData();
    } catch(e) {
      console.error(e);
      this.setState({loading: false, snackbarOpen: true, snackbarMessage: 'Transaction has been cancelled, please try again.'});
    }
  };

  onPickWinnerSubmit = async event => {
    event.preventDefault();
    const { raffle, balance } = this.state;

    if (balance === 0 || balance === '0') {
      this.setState({snackbarOpen: true, snackbarMessage: 'No QTUM in the contract!'});
      return;
    }
    this.setState({snackbarOpen: true, snackbarMessage: 'Please confirm the transaction in the Qtum Light Wallet to complete the operation.'});

    try {
      const tx = await raffle.send('pickWinner');
      this.setState({snackbarOpen: false, loading: true, loadingMessage: 'Writing to the blockchain, please wait...'});
      await raffle.queryConfirmation(tx);
      this.setState({loading: false, snackbarOpen: true, snackbarMessage: 'Picked owner successfully! All Qtum has transferred to the winner!'});
      this.loadData();
    } catch(e) {
      console.error(e);
      this.setState({loading: false, snackbarOpen: true, snackbarMessage: 'Something went wrong! Make sure you are the owner!'});
    }
  };

  renderAppBar() {
    const balance = this.state.balance || 0;
    return (
      <AppBar position="sticky">
        <Toolbar>
          <div className="tool-bar">
            <Typography variant="title" color="inherit">
              <div className="trademark">QTUM RAFFLE™</div>
            </Typography>
            <div className="appbar-info">
              <AlarmIcon /> <div>{this.state.countdownTimer}</div>
              <LoyaltyIcon /> <div>{balance}</div>
            </div>
          </div>
        </Toolbar>
      </AppBar>
    );
  }

  addPaddingZeroIfNeeded = (number) => {
    if (number === 0) return '00';
    return number < 10 && number > 0 ? '0' + number : number;
  };

  startCountdownTimer = (endTime) => {
    if (this.state.endTime === endTime) {
      return;
    }
    const currentTime = (new Date()).getTime();
    const diffTime = endTime - currentTime;
    let duration = moment.duration(diffTime, 'milliseconds');
    const interval = 1000;

    if (refreshIntervalId) {
      clearInterval(refreshIntervalId);
    }
    refreshIntervalId = setInterval(() => {
      duration = moment.duration(duration - interval, 'milliseconds');
      const hours = this.addPaddingZeroIfNeeded(duration.hours());
      const minutes = this.addPaddingZeroIfNeeded(duration.minutes());
      const seconds = this.addPaddingZeroIfNeeded(duration.seconds());
      const countdownTimer = `${hours} : ${minutes} : ${seconds}`;
        this.setState({countdownTimer});
      }, interval);
    this.setState( {endTime} );
  };

  renderSummary() {
    const { balance } = this.state;
    return (
      <div className="App">
        <div className="total-qtum">
          <img src={logo} alt="Qtum Raffle"/>
          <div>{ balance } QTUM</div>
        </div>
        <div className="countdown-timer">{this.state.countdownTimer} </div>
      </div>
    );
  }

  renderLoading() {
    return (
      <Dialog
        open={this.state.loading}
        keepMounted
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description">
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            {this.state.loadingMessage}
          </DialogContentText>
        </DialogContent>
        <LinearProgress />
      </Dialog>
    );
  }

  handleSnackbarClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    this.setState({ snackbarOpen: false });
  };

  renderSnackbar() {
    return (
      <Snackbar
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
        open={this.state.snackbarOpen}
        autoHideDuration={6000}
        onClose={this.handleSnackbarClose}
        ContentProps={{
          'aria-describedby': 'message-id',
        }}
        message={<span id="message-id">{this.state.snackbarMessage}</span>}
        action={[
          <IconButton
            key="close"
            aria-label="Close"
            color="inherit"
            onClick={this.handleSnackbarClose}
          >
            <CloseIcon />
          </IconButton>,
        ]}
      />
    );
  }

  renderFooter() {
    return (
      <footer>
        <div className="footer-container">
          <p>&copy; 2018 QTUM RAFFLE™ | Please install the <a href="https://github.com/QtumLightProject/qtum-light-wallet" target="_blank"> Qtum Light Wallet</a> to play this game | Contact: appbery.digital@gmail.com </p>
        </div>
      </footer>
    );
  }

  renderPlayers() {
    let { players } = this.state;
    if (players && players.length > 0) {
      if (players.length > 4) {
        players = players.slice(0, 4);
      }
      return (
        <Card className="card small-card">
          <div className="card-title">Recent Players</div>
          <hr className="card-title-divider"/>
          <Table>
            <TableBody>
              {players.map((player, index) => {
                return (
                  <TableRow key={index}>
                    <TableCell className="table-cell">{player}</TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </Card>)
    } else {
      return (
        <Card className="card small-card">
          <div className="card-title">Recent Players</div>
          <hr className="card-title-divider"/>
          <p>No players yet, be quick to be the first!</p>
        </Card>)
    }
  }

  renderBody() {
    const { account, walletInitialized } = this.state;
    if (!walletInitialized) {
      return null;
    } else {
      if (account) {
        return (
          <div className="main-container">
            {this.renderPlayers()}
            {this.renderBuy()}
            {this.renderContractInfo()}
          </div>
        );
      } else {
        return (
          <h2>Please sign in Qtum Light Wallet and refresh the page!</h2>
        )
      }
    }
  }

  render() {
    return (
      <MuiThemeProvider theme={theme}>
        <div className="App">
          {this.renderAppBar()}
          {this.renderSummary()}
          {this.renderBody()}
          {this.renderLoading()}
          {this.renderSnackbar()}
          {this.renderFooter()}
        </div>
      </MuiThemeProvider>
    );
  }
}

export default App;
